import React from "react";

interface State {
	/*color : string;*/
	//size : string;
	name : string;	
}

interface Props {
	//color : string;
	//size : string;
	name : string;
}

class Icons extends React.Component<Props, State>{
	constructor(props : any){
		super(props);
		this.state = {
		//	color : "green",
		//	size : props.size,
			name : props.name,
		}
	}

	render() {
		return (
				icon[this.state.name]
		)
	}
}

